$(document).ready(function () {
  $('.slider-about-rest').slick({
    infinite: true,
    slidesToShow: 2,
    slidesToScroll: 1,
    appendArrows: $('.slider-about-rest'),
    prevArrow: $('.prev-arrow__about-rest'),
    nextArrow: $('.next-arrow__about-rest'),
    variableWidth: true,
  });
});
